import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import ExcelReader from './ExcelReader';
import './ExcelCardsRender.css';
import NavbarComponent from './NavbarComponent';
import { useParams } from 'react-router-dom';


const ArtDetail = () => {
  // Using useParams to get the parameters from the URL
  const { artId } = useParams();
  const [artwork, setArtwork] = useState([]);
  const [longi, setlongi] = useState([]);
  const [lati, setlati] = useState([]);
  
  const handleDataLoaded = (data) => {
    const selectedArtwork = data.find((art) => art.ART_ID === artId);

    if (selectedArtwork) {
      setArtwork(selectedArtwork);
      
      const [longitude, latitude] = selectedArtwork.POINT.match(/-?\d+\.\d+/g);

      setlongi(longitude)
      setlati(latitude)
      
     

      
    } else {
      // Handle case when artwork is not found
      console.error(`Artwork with ART_ID ${artId} not found`);
    }
    
  };

  return (
    <div>
      <NavbarComponent/>
      
      <div 
      style={{
        margin: '10px', // Adjust margin as needed
        borderRadius: '10px', // Adjust border radius for rounded corners
        border: '2px solid #333', // Darkened border color
        padding: '10px', // Adjust padding as needed
      }}
      >
        <ExcelReader onDataLoaded={handleDataLoaded} />

        <div style={{ padding: '20px' }}>
          <h1>{artwork.TITLE}</h1>
          

          <img
            src={process.env.PUBLIC_URL + `/img/${artwork.ART_ID}.png`}
            alt={`Artwork: ${artwork.TITLE}`}
            onError={(e) => {
              e.target.src = process.env.PUBLIC_URL + '/img/nopic.png';
            }}
          />
          <h2>Artist: {artwork.ARTIST}</h2>
          <p>Address: {artwork.ADDRESS}</p>
          <p>{artwork.SHORT_DESC}</p>
          <a
            href={`https://www.google.com/maps?q=${lati},${longi}`}
            target="_blank"
            rel="noopener noreferrer"
          >
          Go to Map
          </a>
          

          {/* Add additional details or components related to the artwork */}
        </div>
      </div>
    </div>
  );
};

export default ArtDetail;
