// JSONRender.js
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import ExcelReader from './ExcelReader';
import './ExcelCardsRender.css';
import NavbarComponent from './NavbarComponent';

const NWPAList = () => {
  const [jsonData, setJsonData] = useState([]);

  const handleDataLoaded = (data) => {
    // Filter rows where 'TAB_NAME' is equal to 'Downtown'
    const filteredData = data.filter(row => row['TAB_NAME'] === 'Northwest');
    setJsonData(filteredData);
  };

  return (
    <div>
      <NavbarComponent />
      
      <ExcelReader onDataLoaded={handleDataLoaded} />
      <div className="card-container">
        {jsonData.map((row, rowIndex) => (
          <Link to={`/art-detail/${row['ART_ID']}`} key={row['ART_ID']} className="card-link">
            <div className="card" key={rowIndex}>
              <img
                src={process.env.PUBLIC_URL + `/img/${row['ART_ID']}.png`}
                alt="Artwork"
                onError={(e) => {
                  e.target.src = process.env.PUBLIC_URL + '/img/nopic.png';
                }}
              />
              <h2>{row['TITLE']}</h2>
            </div>
          </Link>
        ))}
      </div>
    </div>
  );
};

export default NWPAList;
